@media (min-width: 320px) {
    .projects {
        margin: 75px auto auto auto;

        &-title {
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            color: $b;
            text-align: center;

            &>span {
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                color: $m-color;
            }
        }

        &-cards {
            margin: 50px auto auto auto;
            width: 100%;

            &__item {
                width: 277px;
                height: 400px;
                background: $w;
                box-shadow: 4px 4px 27px rgba(0, 0, 0, 0.15);
                margin: 46px auto;

                &--text {
                    height: 76px;
                    padding-left: 17px;
                    font-weight: 300;
                    font-size: 22px;
                    line-height: 27px;
                    color: $b;

                    &>b {
                        font-size: 22px;
                        line-height: 27px;
                        color: $b;
                    }
                }

                &--btn {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-left: 117px;
                    width: 140px;
                    height: 41px;
                    background: #1A91FF;
                    border-radius: 5px;
                    border: none;

                    &>span {
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 24px;
                        color: $w;
                    }
                }
            }

            &__item:first-child {
                margin-top: 0;
            }
        }

        @media (min-width: 768px) {
            &-cards {
                display: flex;
                align-items: baseline;
                flex-wrap: wrap;
            }
        }
    }
}