@media (min-width: 320px) {
    .header {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        &-burger {
            display: block;
            position: relative;
            width: 34px;
            height: 20px;
            z-index: 2;
        }

        &-burger span {
            position: absolute;
            background-color: #333333;
            right: 0;
            width: 50%;
            height: 3px;
            top: 9px;
            transition: 0.5s;
            border-radius: 20px;
        }

        &-burger {
            &__active {
                &>span {
                    transform: scale(0);
                }
            }
        }

        &-burger::before {
            content: '';
            background-color: #333333;
            position: absolute;
            width: 100%;
            height: 3px;
            left: 0;
            transition: 0.5s;
            border-radius: 20px;
        }

        &-burger::after {
            content: '';
            background-color: #333333;
            position: absolute;
            width: 80%;
            height: 3px;
            right: 0;
            transition: 0.5s;
            border-radius: 20px;
        }

        &-burger::before {
            top: 0;
        }

        &-burger::after {
            bottom: 0;
        }

        &-burger {
            &__active::before {
                transform: rotate(45deg);
                top: 9px;
            }
            &__active::after {
                transform: rotate(-45deg);
                bottom: 9px;
            }
        }
    }
}