@media (min-width: 320px) {
    .connect {
        margin: 75px auto auto auto;

        &-title {
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            color: $b;
            text-align: center;

            &>span {
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                color: $m-color;
            }
        }

        &-form {
            margin: 28px auto auto auto;

            &__inputs {
                display: flex;
                flex-direction: column;
            }

            &__input {
                margin: 29px auto auto auto;

                &>span {
                    margin-left: 1em;
                    font-weight: 600;
                    font-size: 20px;
                    color: #FF0000;
                }

                &--item {
                    margin-left: 9px;
                    text-indent: 15px;
                    width: 277px;
                    height: 57px;
                    background: #FFFFFF;
                    box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);
                    border: none;
                }

                &--last {
                    margin-left: 0;
                    width: 277px;
                    height: 142px;
                    background: #FFFFFF;
                    box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);
                    border: none;

                    &>span {
                        margin-left: 0;
                    }

                    &>input {
                        width: 100%;
                        height: 100%;
                        text-indent: 15px;
                        border: none;
                    }
                }
            }
        }
    }
}