@media (min-width: 320px) {
    .promo {
        margin: 60px auto auto auto;

        &-title {
            &__name {
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                color: #000000;
                text-align: center;

                &>span {
                    font-size: 24px;
                    font-weight: 600;
                    line-height: 29px;
                    color: $m-color;
                }
            }

            &__text {
                font-weight: 500;
                font-size: 16px;
                line-height: 20px;
                color: #000000;
                text-align: center;
            }
        }

        &-item {
            width: 232px;
            margin: 109px auto auto auto;

            &>img {
                width: 100%;
            }
        }

        &-links {
            margin: 60px auto auto auto;
            width: 197px;
        }
    }
}