@media (min-width: 320px) {
    .footer {
        margin: 68px auto auto auto;
        width: 100%;
        height: 255px;
        background: #FFFFFF;
        box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);

        &-title {
            padding-top: 75px;
            text-align: center;

            &>p {
                color: $b;
            }

            &>span {
                font-weight: 400;
                color: #8F8F8F;
            }
        }

        &-links {
            margin: 29px auto auto auto;
            width: 197px;
        }
    }
}