@media (min-width: 320px) {
    .about {
        margin: 75px auto auto auto;

        &-title {
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            color: $b;
            text-align: center;

            &>span {
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                color: $m-color;
            }
        }

        &-first {
            margin: 58px auto auto auto;

            &__img {
                margin: auto;
                width: 243px;

                &>img {
                    width: 100%;
                    border-radius: 5px;
                }
            }

            &__text {
                text-align: center;
                width: 250px;
                margin: auto;
            }
        }

        &-second {
            margin: 15px auto auto auto;

            &__img {
                margin: auto;
                width: 243px;

                &>img {
                    width: 100%;
                    border-radius: 5px;
                }
            }

            &__text {
                text-align: center;
                width: 250px;
                margin: auto;
            }

            &__text--desk {
                display: none;
            }
        }
    }
}

@media (min-width: 768px) {
    .about {
        &-first {
            display: flex;
            width: 500px;

            &__img {
                margin: unset;
            }

            &__text {
                text-align: start;
                margin-left: 2em;
            }
        }

        &-second {
            margin: 58px auto auto auto;
            display: flex;
            width: 500px;

            &__img {
                margin-left: 2em;
            }

            &__text {
                display: none;
            }

            &__text--desk {
                display: block;
                text-align: start;
                width: 250px;
            }
        }
    }
}